.zonghe {
  float: left;
  width: 1154px;
  height: 821px;
  display: inline-block;
}
.zonghe .comprehensive {
  width: 384px;
  height: 103px;
  margin-bottom: 29px;
  text-align: center;
}
.zonghe .comprehensive a {
  font-size: 44px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 103px;
  letter-spacing: 2px;
  color: #ffffff;
  text-decoration: none;
  text-shadow: 0 0 30px #f4faf5;
}
.zonghe .realtime {
  float: left;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-flow: row wrap;
  align-content: space-between;
  width: 1154px;
  height: 689px;
  overflow: hidden;
}
.zonghe .realtime .semicircleframe {
  position: relative;
  top: 5%;
  left: 5%;
  width: 32%;
  height: 50%;
}
.zonghe .realtime .one {
  margin-top: 70px;
  margin-left: 79px;
  margin-bottom: 0px;
  font-size: 30px;
  font-weight: normal;
  font-stretch: normal;
  color: #ffffff;
  width: 100%;
  height: 25%;
}
.zonghe .realtime span {
  font-size: 66px;
  font-family: PlakatDBNormal;
  font-weight: normal;
  font-stretch: normal;
  line-height: 76px;
  color: #00ffff;
}
.fuwu {
  margin-left: 85px;
  font-size: 36px;
  font-weight: normal;
  font-stretch: normal;
  color: #ffffff;
}
